import React from 'react';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

//css
import '../../static/styles/components/DET/MentorTripCard.css';

//components
import Card from './MentorDetailsSpecificCard';

// config
import isMobile from '../../static/utils/applicationUrls';

export default function MentorTripCard(props) {
	const responsive = {
		superLargeDesktop: {
			breakpoint: { max: 4000, min: 3000 },
			items: 6,
		},
		desktop: {
			breakpoint: { max: 3000, min: 1024 },
			items: 5,
		},
		tablet: {
			breakpoint: { max: 1024, min: 730 },
			items: 4,
		},
		mobile: {
			breakpoint: { max: 730, min: 0 },
			items: 1,
		},
	};

	return (
		<>
			<Carousel
				ssr={!isMobile}
				deviceType={!isMobile ? 'desktop' : null}
				responsive={responsive}>
				{props.data.map((x) => x.map((tripm) =>
					<div className='TripMentorDetails'>
						<a href={tripm.linkedin} className='linkedin' target='_blank'>
							<Card
								name={tripm.name}
								image={tripm.image}
								company={tripm.company}
								desig={tripm.designation}
								logo={tripm.logo}
							/>
						</a>
					</div>
				))}
			</Carousel>
		</>
	);
}
