//react
import React from 'react';
import { Tab, Tabs } from 'react-bootstrap';
// import 'react-responsive-carousel/lib/styles/carousel.min.css';
// import { Carousel } from 'react-responsive-carousel';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

//css
import '../../static/styles/components/DET/TripItinerary1.css';

//images
import EmpireIntro from '../../static/images/Icons/trip-itinerary-empire-intro.svg';
import Plane from '../../static/images/Icons/plane_in_button.png';

//components
import MentorCarouselCard from './MentorCarouselCard';
import PersonalCard from './PersonalCard';
import MentorSuperCard from './MentorSuperCard';
import MentorTripCard from './MentorTripCard';

//utils
import { ITINERARY } from '../../static/utils/TripItinerary';

// config
import isMobile from '../../static/utils/applicationUrls';

export default function TripItinerary1() {
	const responsive = {
		superLargeDesktop: {
			breakpoint: { max: 4000, min: 3000 },
			items: 1,
		},
		desktop: {
			breakpoint: { max: 3000, min: 1024 },
			items: 1,
		},
		tablet: {
			breakpoint: { max: 1024, min: 464 },
			items: 1,
		},
		mobile: {
			breakpoint: { max: 464, min: 0 },
			items: 1,
		},
	};
	const TabsData = [
		{
			eventKey: 'Technology',
			title: 'Technology',
			roles: ITINERARY['Technology '].map(function (a, b) {
				return a.roles;
			}),
			superm: ITINERARY['Technology '].map(function (a, b) {
				return a.supermentor;
			}),
			tripm: ITINERARY['Technology '].map(function (a, b) {
				return a.tripmentors;
			}),
		},
		{
			eventKey: 'Business Management',
			title: 'Business Management',
			roles: ITINERARY['Business '].map(function (a, b) {
				return a.roles;
			}),
			superm: ITINERARY['Business '].map(function (a, b) {
				return a.supermentor;
			}),
			tripm: ITINERARY['Business '].map(function (a, b) {
				return a.tripmentors;
			}),
		},
		{
			eventKey: 'Creativity',
			title: 'Creativity',
			roles: ITINERARY['Creativity '].map(function (a, b) {
				return a.roles;
			}),
			tripm: ITINERARY['Creativity '].map(function (a, b) {
				return a.tripmentors;
			}),
		},
		{
			eventKey: 'Personality',
			title: 'Personality',
			role1: ITINERARY['Personality '].map(function (a, b) {
				return a.role1;
			}),
			role2: ITINERARY['Personality '].map(function (a, b) {
				return a.role2;
			}),
			role3: ITINERARY['Personality '].map(function (a, b) {
				return a.role3;
			}),
		},
	];
	const TabElements = TabsData.map((tab, i) => {
		return (
			<Tab
				key={i}
				eventKey={tab.eventKey}
				title={tab.title}
				className='IternaryTab-content'>
				{tab.title === 'Technology' ||
					tab.title === 'Business Management' ? (
					<>
						<Carousel
							ssr={!isMobile}
							deviceType={!isMobile ? 'desktop' : null}
							responsive={responsive}
							showDots={true}
							className='mentoor'>
							{tab.roles.map((x) =>
								x.map((roles) => (
									<MentorCarouselCard
										title={roles.title}
										skills={roles.skills}
										topics={roles.topics}
										projects={roles.projects}
										name={roles.mentorname}
										image={roles.mentorimg}
										company={roles.mentorcompany}
										desig={roles.mentordesig}
										linkedin={roles.linkedin}
										logo={roles.logo}
									/>
								))
							)}
						</Carousel>
						{tab.superm.map((superm) => (
							<MentorSuperCard
								name={superm.name}
								image={superm.image}
								company={superm.company}
								desig={superm.designation}
								linkedin={superm.linkedin}
								experiences={superm.experiences}
								logo={superm.logo}
							/>
						))}
						<div className='ItineraryTripCard'>
							<div className='ItineraryTripCardContent'>
								<h2 className='ItineraryTripCardHeading'>
									Trip Mentors
								</h2>
								<MentorTripCard data={tab.tripm} />
							</div>
						</div>
					</>
				) : tab.title === 'Creativity' ? (
					<>
						{tab.roles.map((x) =>
							x.map((roles) => (
								<MentorCarouselCard
									title={roles.title}
									skills={roles.skills}
									topics={roles.topics}
									projects={roles.projects}
									name={roles.mentorname}
									image={roles.mentorimg}
									company={roles.mentorcompany}
									desig={roles.mentordesig}
									linkedin={roles.linkedin}
									logo={roles.logo}
								/>
							))
						)}
						<div className='ItineraryTripCard'>
							<div className='ItineraryTripCardContent'>
								<h2 className='ItineraryTripCardHeading'>
									Trip Mentors
								</h2>
								<MentorTripCard data={tab.tripm} />
							</div>
						</div>
					</>
				) : (
					<div className='PersonalTab'>
						<h2 className='ItineraryPersonalCardHeading'>
							Productivity
						</h2>
						<Carousel
							ssr={!isMobile}
							deviceType={!isMobile ? 'desktop' : null}
							responsive={responsive}
							showDots={true}
							className='mentoor'>
							{tab.role1.map((x) =>
								x.map((roles) => (
									<PersonalCard
										title={roles.title}
										skills={roles.skills}
										topics={roles.topics}
										projects={roles.projects}
										mentorimg={roles.mentorimg}
									/>
								))
							)}
						</Carousel>
						<h2 className='ItineraryPersonalCardHeading'>
							Core Communication
						</h2>
						<Carousel
							ssr={!isMobile}
							deviceType={!isMobile ? 'desktop' : null}
							responsive={responsive}
							showDots={true}
							className='mentoor'>
							{tab.role2.map((x) =>
								x.map((roles) => (
									<PersonalCard
										title={roles.title}
										skills={roles.skills}
										topics={roles.topics}
										projects={roles.projects}
										mentorimg={roles.mentorimg}
									/>
								))
							)}
						</Carousel>
						<h2 className='ItineraryPersonalCardHeading'>
							3 Pillars of Dream Exploration
						</h2>
						<Carousel
							ssr={!isMobile}
							deviceType={!isMobile ? 'desktop' : null}
							responsive={responsive}
							showDots={true}
							className='mentoor'>
							{tab.role3.map((x) =>
								x.map((roles) => (
									<PersonalCard
										title={roles.title}
										skills={roles.skills}
										topics={roles.topics}
										projects={roles.projects}
										mentorimg={roles.mentorimg}
									/>
								))
							)}
						</Carousel>
					</div>
				)}
			</Tab>
		);
	});

	return (
		<div className='TripItinerary' id='itinerary'>
			<div className='TripItineraryContent'>
				<div className='TripItineraryText'>
					<div className='TripItineraryTitle'>
						{' '}
						Trip
						<span className='TripItineraryTitleOrange'>
							{' '}
							Itinerary
						</span>
					</div>
					<div className='TripItineraryIntro'>
						The route of this trip involves various Aha moments, exciting checkpoints
						and a divine destination. You are going to unlock the hidden mysteries of life
						along with the community of moonshot thinkers.
					</div>
				</div>

				<div className='TripItineraryBox'>
					<div className='hideInMobileScreenOnly'>
						{TabsData.length > 5 ? (
							<div className='shadow-wrapper itinerary-wrapper'></div>
						) : null}
						<Tabs
							defaultActiveKey='Technology'
							id='uncontrolled-tab-example'
							className='ItineraryTabs'>
							{TabElements}
						</Tabs>
					</div>

					<div className='showInMobileScreenOnly'>
						<Tabs defaultActiveKey='Technology'>{TabElements}</Tabs>
					</div>
				</div>
			</div>
			<div className='empire-intro'>
				<main className='empire-intro-content'>
					<h2>
						Explorin<span className='orange-text'> Empire</span>
					</h2>
					<p>
						An army of 50+ passionate Mentors will be enabling you throughout
						the Dream Exploration Trip to guide you to realize your career passion.
					</p>
					<button
						className='button'
						onClick={() => window.location.assign('/empire')}>
						Explore the Empire
						<img
							alt='Plane'
							src={Plane}
							className='PlaneInButton'
						/>
					</button>
				</main>
				<img src={EmpireIntro} alt='Empire' />
			</div>
		</div>
	);
}
